#root {
  text-align: center;
  display: flex;
  justify-content: center;
  width: 100vw;
  min-height: 100vh;
  overflow: hidden;
  /* background: linear-gradient(#fff, #999); */
  background: linear-gradient(#e66465, #9198e5);
}

* {
  user-select: none;
}

#root > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.app {
  overflow: hidden;
}

.app > div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.row {
  flex-direction: row !important;
}

.row > * {
  margin: 5px;
}
a {
  text-decoration: none;
  color: #fff;
}

h1 {
  font-family: "Damion", cursive;
  color: #fff;
  text-shadow: 0px 0px 60px 0px rgba(0, 0, 0, 0.3);
}

h2 {
  color: #fff;
}

.swipe {
  position: absolute;
}

.cardContainer {
  width: 90vw;
  max-width: 260px;
  height: 300px;
}

.card {
  position: relative;
  left: 60%;
  top: 20%;
  width: 80vw;
  max-width: 260px;
  height: 300px;
  /* box-shadow: 0px 0px 60px 0px rgba(0, 0, 0, 0.3); */
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}

.cardContent {
  width: 100%;
  height: 100%;
}

.swipe:last-of-type {
}

.card h3 {
  position: absolute;
  bottom: 0;
  margin: 10px;
  color: #fff;
}

.infoText {
  width: 100%;
  justify-content: center;
  display: flex;
  color: #fff;
  animation-name: popup;
  animation-duration: 800ms;
}

.buttons {
  margin: 20px;
  display: flex;
  flex-wrap: wrap;
}

@media (max-width: 625px) {
  .buttons {
    flex-direction: column;
  }
}

.buttons button {
  flex-shrink: 0;
  padding: 10px;
  border-radius: 5px;
  border: none;
  color: #fff;
  font-size: 18px;
  background-color: #9198e5;
  transition: 200ms;
  margin: 10px;
  font-weight: bolder;
  width: 160px;
  box-shadow: 0px 0px 30px 0px rgba(0, 0, 0, 0.1);
}

.buttons button:hover {
  transform: scale(1.05);
}

@keyframes popup {
  0% {
    transform: scale(1, 1);
  }
  10% {
    transform: scale(1.1, 1.1);
  }
  30% {
    transform: scale(0.9, 0.9);
  }
  50% {
    transform: scale(1, 1);
  }
  57% {
    transform: scale(1, 1);
  }
  64% {
    transform: scale(1, 1);
  }
  100% {
    transform: scale(1, 1);
  }
}

.cardContainer {
  display: flex;
}
.card {
  background: rgb(177, 93, 162);
  background: -moz-linear-gradient(
    90deg,
    rgba(177, 93, 162, 1) 2%,
    rgba(19, 204, 225, 1) 30%,
    rgba(70, 45, 161, 1) 72%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(177, 93, 162, 1) 2%,
    rgba(19, 204, 225, 1) 30%,
    rgba(70, 45, 161, 1) 72%
  );
  background: linear-gradient(
    90deg,
    rgba(177, 93, 162, 1) 2%,
    rgba(19, 204, 225, 1) 30%,
    rgba(70, 45, 161, 1) 72%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#b15da2",endColorstr="#462da1",GradientType=1);
  color: #fff;
}

.cursor-pointer {
  cursor: pointer;
}
